import React, { useState, useEffect, useCallback } from 'react';
import { userApi, roleApi } from '../services/api';

const UsersPage = () => {
  const [users, setUsers] = useState([]);
  const [roles, setRoles] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [showRoleModal, setShowRoleModal] = useState(false);
  const [editingUser, setEditingUser] = useState(null);
  const [editingRole, setEditingRole] = useState(null);
  const [filters, setFilters] = useState({ search: '', role: '', isActive: '' });
  const [pagination, setPagination] = useState({ page: 1, limit: 20, total: 0 });
  const [selectedPermissions, setSelectedPermissions] = useState([]);
  const [permissions, setPermissions] = useState([]);

  const [formData, setFormData] = useState({
    email: '',
    password: '',
    firstName: '',
    lastName: '',
    phone: '',
    role: '',
    isActive: true,
  });

  const [roleFormData, setRoleFormData] = useState({
    name: '',
    displayName: '',
    description: '',
    level: 0,
    color: '#4F46E5',
    permissions: [],
  });

  const loadUsers = useCallback(async () => {
    setLoading(true);
    try {
      const params = {
        page: pagination.page,
        limit: pagination.limit,
        ...filters,
      };
      if (!params.search) delete params.search;
      if (!params.role) delete params.role;
      if (params.isActive === '') delete params.isActive;

      const response = await userApi.getAll(params);
      if (response.success) {
        setUsers(response.data.users);
        setPagination(prev => ({ ...prev, ...response.data.pagination }));
      }
    } catch (error) {
      console.error('Failed to load users:', error);
    }
    setLoading(false);
  }, [pagination.page, filters]);

  const loadRoles = async () => {
    try {
      const response = await roleApi.getAll();
      if (response.success) {
        setRoles(response.data.roles);
      }
    } catch (error) {
      console.error('Failed to load roles:', error);
    }
  };

  const loadPermissions = async () => {
    try {
      const response = await roleApi.getPermissions();
      if (response.success) {
        setPermissions(response.data.permissions);
        setPermissionsGrouped(response.data.groupedPermissions);
      }
    } catch (error) {
      console.error('Failed to load permissions:', error);
    }
  };

  const [permissionsGrouped, setPermissionsGrouped] = useState({});

  useEffect(() => {
    loadUsers();
    loadRoles();
  }, [loadUsers]);

  const handleFilterChange = (key, value) => {
    setFilters(prev => ({ ...prev, [key]: value }));
    setPagination(prev => ({ ...prev, page: 1 }));
  };

  const handlePageChange = (page) => {
    setPagination(prev => ({ ...prev, page }));
  };

  const handleOpenUserModal = (user = null) => {
    if (user) {
      setEditingUser(user);
      setFormData({
        email: user.email,
        password: '',
        firstName: user.firstName,
        lastName: user.lastName,
        phone: user.phone || '',
        role: user.role?._id || user.role || '',
        isActive: user.isActive,
      });
    } else {
      setEditingUser(null);
      setFormData({
        email: '',
        password: '',
        firstName: '',
        lastName: '',
        phone: '',
        role: '',
        isActive: true,
      });
    }
    setShowModal(true);
  };

  const handleOpenRoleModal = (role = null) => {
    if (role) {
      setEditingRole(role);
      setRoleFormData({
        name: role.name,
        displayName: role.displayName,
        description: role.description || '',
        level: role.level,
        color: role.color,
        permissions: role.permissions?.map(p => p._id) || [],
      });
      setSelectedPermissions(role.permissions?.map(p => p._id) || []);
    } else {
      setEditingRole(null);
      setRoleFormData({
        name: '',
        displayName: '',
        description: '',
        level: 0,
        color: '#4F46E5',
        permissions: [],
      });
      setSelectedPermissions([]);
    }
    setShowRoleModal(true);
    loadPermissions();
  };

  const handleSaveUser = async () => {
    try {
      if (editingUser) {
        await userApi.update(editingUser._id, formData);
      } else {
        await userApi.create(formData);
      }
      setShowModal(false);
      loadUsers();
    } catch (error) {
      console.error('Failed to save user:', error);
      alert(error.message || 'Failed to save user');
    }
  };

  const handleSaveRole = async () => {
    try {
      const data = { ...roleFormData, permissions: selectedPermissions };
      if (editingRole) {
        await roleApi.update(editingRole._id, data);
      } else {
        await roleApi.create(data);
      }
      setShowRoleModal(false);
      loadRoles();
    } catch (error) {
      console.error('Failed to save role:', error);
      alert(error.message || 'Failed to save role');
    }
  };

  const handleDeleteUser = async (userId) => {
    if (!window.confirm('Are you sure you want to delete this user?')) return;
    try {
      await userApi.delete(userId);
      loadUsers();
    } catch (error) {
      console.error('Failed to delete user:', error);
      alert(error.message || 'Failed to delete user');
    }
  };

  const handleToggleUserStatus = async (userId) => {
    try {
      await userApi.toggleStatus(userId);
      loadUsers();
    } catch (error) {
      console.error('Failed to toggle user status:', error);
      alert(error.message || 'Failed to toggle user status');
    }
  };

  const handleDeleteRole = async (roleId) => {
    if (!window.confirm('Are you sure you want to delete this role?')) return;
    try {
      await roleApi.delete(roleId);
      loadRoles();
    } catch (error) {
      console.error('Failed to delete role:', error);
      alert(error.message || 'Failed to delete role');
    }
  };

  const togglePermission = (permissionId) => {
    setSelectedPermissions(prev => 
      prev.includes(permissionId)
        ? prev.filter(id => id !== permissionId)
        : [...prev, permissionId]
    );
  };

  const toggleAllPermissions = (category) => {
    const categoryPerms = permissions.filter(p => p.category === category);
    const categoryIds = categoryPerms.map(p => p._id);
    const allSelected = categoryIds.every(id => selectedPermissions.includes(id));
    
    if (allSelected) {
      setSelectedPermissions(prev => prev.filter(id => !categoryIds.includes(id)));
    } else {
      setSelectedPermissions(prev => [...new Set([...prev, ...categoryIds])]);
    }
  };

  if (loading && users.length === 0) {
    return (
      <>
        <header className="main-header">
          <h1 className="page-title"><i className="bi bi-people"></i>User Management</h1>
        </header>
        <div className="page-content">
          <div className="d-flex justify-content-center align-items-center" style={{ minHeight: '400px' }}>
            <div className="loading-spinner"></div>
          </div>
        </div>
      </>
    );
  }

  return (
    <>
      <header className="main-header">
        <div className="d-flex justify-content-between align-items-center flex-wrap gap-2">
          <h1 className="page-title"><i className="bi bi-people"></i>User Management</h1>
          <div className="d-flex gap-2">
            <button className="btn btn-secondary" onClick={() => handleOpenRoleModal()}>
              <i className="bi bi-shield"></i> Manage Roles
            </button>
            <button className="btn btn-primary" onClick={() => handleOpenUserModal()}>
              <i className="bi bi-plus-lg"></i> Add User
            </button>
          </div>
        </div>
      </header>

      <div className="page-content">
        {/* Filters */}
        <div className="card mb-4">
          <div className="card-body">
            <div className="row g-3">
              <div className="col-md-4">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Search users..."
                  value={filters.search}
                  onChange={(e) => handleFilterChange('search', e.target.value)}
                />
              </div>
              <div className="col-md-3">
                <select
                  className="form-select"
                  value={filters.role}
                  onChange={(e) => handleFilterChange('role', e.target.value)}
                >
                  <option value="">All Roles</option>
                  {roles.map(role => (
                    <option key={role._id} value={role._id}>{role.displayName}</option>
                  ))}
                </select>
              </div>
              <div className="col-md-3">
                <select
                  className="form-select"
                  value={filters.isActive}
                  onChange={(e) => handleFilterChange('isActive', e.target.value)}
                >
                  <option value="">All Status</option>
                  <option value="true">Active</option>
                  <option value="false">Inactive</option>
                </select>
              </div>
            </div>
          </div>
        </div>

        {/* Users Table */}
        <div className="card">
          <div className="card-body p-0">
            <div className="table-container">
              <table className="table">
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Email</th>
                    <th>Role</th>
                    <th>Status</th>
                    <th>Last Login</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {users.map(user => (
                    <tr key={user._id}>
                      <td>
                        <div className="d-flex align-items-center gap-2">
                          <div className="avatar bg-primary text-white rounded-circle d-flex align-items-center justify-content-center" style={{ width: 32, height: 32, fontSize: '0.875rem' }}>
                            {user.firstName?.[0]}{user.lastName?.[0]}
                          </div>
                          <span>{user.firstName} {user.lastName}</span>
                        </div>
                      </td>
                      <td>{user.email}</td>
                      <td>
                        <span className="badge" style={{ backgroundColor: user.role?.color || '#6B7280' }}>
                          {user.role?.displayName || 'N/A'}
                        </span>
                      </td>
                      <td>
                        <span className={`badge ${user.isActive ? 'badge-success' : 'badge-secondary'}`}>
                          {user.isActive ? 'Active' : 'Inactive'}
                        </span>
                      </td>
                      <td className="text-muted">
                        {user.lastLogin ? new Date(user.lastLogin).toLocaleDateString() : 'Never'}
                      </td>
                      <td>
                        <div className="d-flex gap-1">
                          <button className="btn btn-sm btn-outline-primary" onClick={() => handleOpenUserModal(user)}>
                            <i className="bi bi-pencil"></i>
                          </button>
                          <button 
                            className={`btn btn-sm ${user.isActive ? 'btn-outline-warning' : 'btn-outline-success'}`}
                            onClick={() => handleToggleUserStatus(user._id)}
                          >
                            <i className={`bi ${user.isActive ? 'bi-pause' : 'bi-play'}`}></i>
                          </button>
                          <button className="btn btn-sm btn-outline-danger" onClick={() => handleDeleteUser(user._id)}>
                            <i className="bi bi-trash"></i>
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>

            {/* Pagination */}
            {pagination.pages > 1 && (
              <div className="d-flex justify-content-center p-3 border-top">
                <nav>
                  <ul className="pagination mb-0">
                    <li className={`page-item ${pagination.page === 1 ? 'disabled' : ''}`}>
                      <button className="page-link" onClick={() => handlePageChange(pagination.page - 1)}>Previous</button>
                    </li>
                    {[...Array(pagination.pages)].map((_, idx) => (
                      <li key={idx} className={`page-item ${pagination.page === idx + 1 ? 'active' : ''}`}>
                        <button className="page-link" onClick={() => handlePageChange(idx + 1)}>{idx + 1}</button>
                      </li>
                    ))}
                    <li className={`page-item ${pagination.page === pagination.pages ? 'disabled' : ''}`}>
                      <button className="page-link" onClick={() => handlePageChange(pagination.page + 1)}>Next</button>
                    </li>
                  </ul>
                </nav>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* User Modal */}
      {showModal && (
        <div className="modal show d-block" style={{ backgroundColor: 'rgba(0,0,0,0.5)' }}>
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">{editingUser ? 'Edit User' : 'Add New User'}</h5>
                <button type="button" className="btn-close" onClick={() => setShowModal(false)}></button>
              </div>
              <div className="modal-body">
                <div className="row g-3">
                  <div className="col-md-6">
                    <label className="form-label">First Name</label>
                    <input type="text" className="form-control" value={formData.firstName} onChange={(e) => setFormData({ ...formData, firstName: e.target.value })} required />
                  </div>
                  <div className="col-md-6">
                    <label className="form-label">Last Name</label>
                    <input type="text" className="form-control" value={formData.lastName} onChange={(e) => setFormData({ ...formData, lastName: e.target.value })} required />
                  </div>
                  <div className="col-md-6">
                    <label className="form-label">Email</label>
                    <input type="email" className="form-control" value={formData.email} onChange={(e) => setFormData({ ...formData, email: e.target.value })} disabled={!!editingUser} required />
                  </div>
                  <div className="col-md-6">
                    <label className="form-label">Password {!editingUser && '(required)'}</label>
                    <input type="password" className="form-control" value={formData.password} onChange={(e) => setFormData({ ...formData, password: e.target.value })} required={!editingUser} />
                  </div>
                  <div className="col-md-6">
                    <label className="form-label">Phone</label>
                    <input type="text" className="form-control" value={formData.phone} onChange={(e) => setFormData({ ...formData, phone: e.target.value })} />
                  </div>
                  <div className="col-md-6">
                    <label className="form-label">Role</label>
                    <select className="form-select" value={formData.role} onChange={(e) => setFormData({ ...formData, role: e.target.value })} required>
                      <option value="">Select Role</option>
                      {roles.map(role => (
                        <option key={role._id} value={role._id}>{role.displayName}</option>
                      ))}
                    </select>
                  </div>
                  <div className="col-12">
                    <div className="form-check">
                      <input type="checkbox" className="form-check-input" id="isActive" checked={formData.isActive} onChange={(e) => setFormData({ ...formData, isActive: e.target.checked })} />
                      <label className="form-check-label" htmlFor="isActive">Active</label>
                    </div>
                  </div>
                </div>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-secondary" onClick={() => setShowModal(false)}>Cancel</button>
                <button type="button" className="btn btn-primary" onClick={handleSaveUser}>Save</button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Role Modal */}
      {showRoleModal && (
        <div className="modal show d-block" style={{ backgroundColor: 'rgba(0,0,0,0.5)' }}>
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">{editingRole ? 'Edit Role' : 'Create New Role'}</h5>
                <button type="button" className="btn-close" onClick={() => setShowRoleModal(false)}></button>
              </div>
              <div className="modal-body">
                <div className="row g-3">
                  <div className="col-md-4">
                    <label className="form-label">Role Name</label>
                    <input type="text" className="form-control" value={roleFormData.name} onChange={(e) => setRoleFormData({ ...roleFormData, name: e.target.value })} disabled={editingRole?.isSystem} required />
                  </div>
                  <div className="col-md-4">
                    <label className="form-label">Display Name</label>
                    <input type="text" className="form-control" value={roleFormData.displayName} onChange={(e) => setRoleFormData({ ...roleFormData, displayName: e.target.value })} required />
                  </div>
                  <div className="col-md-2">
                    <label className="form-label">Level</label>
                    <input type="number" className="form-control" value={roleFormData.level} onChange={(e) => setRoleFormData({ ...roleFormData, level: parseInt(e.target.value) })} min="0" max="100" />
                  </div>
                  <div className="col-md-2">
                    <label className="form-label">Color</label>
                    <input type="color" className="form-control form-control-color" value={roleFormData.color} onChange={(e) => setRoleFormData({ ...roleFormData, color: e.target.value })} />
                  </div>
                  <div className="col-12">
                    <label className="form-label">Description</label>
                    <textarea className="form-control" value={roleFormData.description} onChange={(e) => setRoleFormData({ ...roleFormData, description: e.target.value })} rows="2"></textarea>
                  </div>
                </div>

                <hr />

                <h6>Permissions</h6>
                <div className="row">
                  {Object.entries(permissionsGrouped).map(([category, perms]) => (
                    <div key={category} className="col-md-4 mb-3">
                      <div className="card">
                        <div className="card-header d-flex justify-content-between align-items-center py-2">
                          <span className="text-capitalize fw-semibold">{category}</span>
                          <button 
                            className="btn btn-sm btn-link"
                            onClick={() => toggleAllPermissions(category)}
                          >
                            Toggle All
                          </button>
                        </div>
                        <div className="card-body py-2" style={{ maxHeight: '200px', overflowY: 'auto' }}>
                          {perms.map(perm => (
                            <div key={perm._id} className="form-check">
                              <input 
                                type="checkbox" 
                                className="form-check-input" 
                                id={`perm-${perm._id}`}
                                checked={selectedPermissions.includes(perm._id)}
                                onChange={() => togglePermission(perm._id)}
                              />
                              <label className="form-check-label" htmlFor={`perm-${perm._id}`}>
                                {perm.displayName}
                              </label>
                            </div>
                          ))}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-secondary" onClick={() => setShowRoleModal(false)}>Cancel</button>
                <button type="button" className="btn btn-primary" onClick={handleSaveRole}>Save Role</button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Roles List */}
      <div className="mt-4">
        <h5>Available Roles</h5>
        <div className="row g-3">
          {roles.map(role => (
            <div key={role._id} className="col-md-3">
              <div className="card">
                <div className="card-body">
                  <div className="d-flex justify-content-between align-items-start">
                    <div>
                      <span className="badge mb-2" style={{ backgroundColor: role.color }}>{role.displayName}</span>
                      <h6 className="mb-1">{role.name}</h6>
                      <p className="text-muted small mb-2">{role.description}</p>
                      <small className="text-muted">{role.permissions?.length || 0} permissions</small>
                    </div>
                    {!role.isSystem && (
                      <button className="btn btn-sm btn-outline-danger" onClick={() => handleDeleteRole(role._id)}>
                        <i className="bi bi-trash"></i>
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default UsersPage;
